////
/// Pathways Cards
/// @group components
////


.pathway-card {
    color: $color-black;
    width: 100%;
    display: block;
    border-bottom: 0;
    margin-bottom: $measure-large;
    position: relative;
    text-align: center;

    @supports (display: flex) {
        display: flex;
        flex-direction: row;
    }

    @include media("above:small") {
        background-color: $color-lightgray;
    }

    @include media("below:medium") {
        @supports (display: flex) {
            flex-wrap: wrap;
        }
    }

    .pathway-card__image {
        height: 100%;
        align-self: center;
        display: block;
        padding: 0;
        overflow: hidden;

        img {
            height: auto;
            min-width: 100%;
            max-width: 200vw;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translateX(-50%) translateY(-50%);
        }

        &.pathway-card__left,
        &.pathway-card__right {
            max-width: 50%;


            @include media("below:small") {
                display: none;
            }
        }

        &.pathway-card__full {
            width: 100%;
            height: 100%;
            position: absolute;
            top:  0;
            left: 0;
            z-index: z-index(default);

            @include media("below:medium") {
                max-width: auto;
            }

            img {
                filter: brightness(25%);
            }
        }
    }
}

[class*="pathway-card--"] {
    border-bottom-width: 0;

    &:before {
        @include transition(border-color);
        content: "";
        border: $border-width-hairline $border-style-default rgba($color-white, 0.5);
        position: absolute;
        pointer-events: none;
        z-index: z-index(above);

        @include media("below:medium") {
            top:    $measure-small;
            right:  $measure-small;
            bottom: $measure-small;
            left:   $measure-small;
        }

        @include media("above:medium") {
            top:    $measure-medium;
            right:  $measure-medium;
            bottom: $measure-medium;
            left:   $measure-medium;
        }
    }

    &:hover,
    &:focus,
    &:active {
        background-color: inherit;
        color: inherit;

        &:before {
            z-index: z-index(navigation);
        }
    }

    &:link:hover,
    &:link:focus,
    &:link:active {
        &:before {
            border-color: $color-white;
        }

        h3 {
            text-decoration: underline;
        }
    }
}

.pathway-card__left,
.pathway-card__right,
.pathway-card__full {
    background-color: $color-lightgray;
    display: flex;
    flex-direction: column;
    position: relative;

    @include media("below:medium") {
        min-height: 6rem;
    }

    @include media("above:medium") {
        min-height: 12rem;
    }

    h3,
    p {
        color: $color-black;
        position: relative;
        z-index: z-index(default);
    }

    h3 {
        flex: 1 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 0;
        font-weight: $font-weight-bold;
    }

    p {
        margin-bottom: $measure-medium;
        line-height: $line-height-default;

        @include media("below:medium") {
            display: none;
        }
    }
}

.pathway-card__left,
.pathway-card__right {
    @include media("below:small") {
        flex: 1 0 100%;
    }

    @include media("above:small") {
        flex: 0 1 50%;
    }

    :not(.pathway-card__image) + & {
        @include media("below:small") {
            margin-top: $measure-large;
        }
    }

    h3,
    p {
        padding: 0 $measure-medium;
    }
}

.pathway-card__left {
    @include media("below:medium") {
        padding: $measure-small 0 $measure-small $measure-small;
    }

    @include media("above:medium") {
        padding: $measure-medium 0 $measure-medium $measure-medium;
    }

    &[class*="pathway-card--"] {
        &:before {
            @include media("above:medium") {
                right: 0;
            }
        }
    }
}

.pathway-card__right {
    @include media("below:medium") {
        padding: $measure-small $measure-small $measure-small 0;
    }

    @include media("above:medium") {
        padding: $measure-medium $measure-medium $measure-medium 0;
    }

    &[class*="pathway-card--"] {
        &:before {
            @include media("above:medium") {
                left: 0;
            }
        }
    }
}

.pathway-card__full {
    flex: 1 0 100%;

    @include media("below:medium") {
        padding: $measure-small;
    }

    @include media("above:medium") {
        padding: $measure-medium;
    }

    h3,
    p {
        color: $color-white;
    }
}



$level-colors: (
    undergraduate: ($color-flamingo, $color-yellow),
    masters: ($color-prussian, $color-prussian),
    mba: ($color-sherpa, $color-sherpa),
    executive-education: ($color-vivid, $color-vivid),
    phd: ($color-allports, $color-montecarlo)
);

@each $level, $colors in $level-colors {
    $primary-color:   nth($colors, 1);
    $secondary-color: nth($colors, 2);
    .pathway-card--#{$level} {
        &:before {
            border-color: rgba($secondary-color, 0.5) !important;
        }

        &:link:hover,
        &:link:focus,
        &:link:active {
            z-index: z-index(above);

            &:before {
                border-color: $secondary-color !important;
            }
        }

        &[class*="pathway-card__"],
        [class*="pathway-card__"] {
            background-color: mix($color-white, $primary-color, 90%) !important;
        }
    }
}
