///
// Footer Meta
// @group components
///


.footer-meta {
    color: $color-white;
    position: relative;

    &:before {
        @include absolute(top);
        @include non-selectable;
        content: "";
        width: 200vw;
        height: 100%;
        pointer-events: none;

        @include media("below:medium") {
            max-width: calc(100% + #{$measure-medium});
        }
    }

    &.section--undergraduate {
        &:before {
            background-color: $color-flamingo;
        }
    }

    &.section--executive-education {
        &:before {
            background-color: $color-vivid;
        }
    }

    &.section--masters {
        &:before {
            background-color: $color-prussian;
        }
    }

    &.section--mba {
        &:before {
            background-color: $color-sherpa;
        }
    }
}

.footer-meta__card {
    @include media("below:small") {
        width: grid-column-width(12);
        padding: 0 $measure-small;
    }

    @include media("above:small") {
        width: grid-column-width(6);
    }

    h5 {
        font-weight: $font-weight-bold;
        text-transform: uppercase;
    }

    a {
        @include anchor(skate--dark-bg);
        vertical-align: top;
    }
}
