/**
 * Pagination
 */
.pagination {
    width: 100%;
    margin: 1em 0px 1em;
    text-align: center;
    display: inline-block;
    list-style: outside none none;
    line-height: 100%;
    font-weight: 600;

    .previous, .number, .after, .ellipsis {
        padding: 4px 8px;
        margin: 10px 0 10px 5px;
        background-color: #EEEEEE;
    }

    .previous, .number, .after {
        border: 1px solid transparent;
    }

    .number, .ellipsis {
        display: none;
    }

    .selected {
        display: inline;
        margin: 10px 0 10px 5px;
        color: #6B6B6B;
        border: 1px solid transparent;

        &:before {
            content: "Page ";
        }
    }

    .previous {
        &:before {
            content: "< ";
        }
    }

    .after {
        &:after {
            content: " >";
        }
    }

    a {
        text-decoration: none;
        color: #000000;
        background-color: #EEEEEE;
        border: 1px solid transparent;
        display: inline;

        &:link {
            color: #000000;
        }
        &:visited {
            color: #000000;
        }
        &:hover {
            color: #B91B1B;
            background-color: #EEEEEE;
            border: 1px solid #B91B1B;
        }
        &:active, &:focus {
            color: white;
            background-color: #B91B1B;
            border: 1px solid transparent;
        }
    }

    .disabled {
        color: #6B6B6B;
        background-color: #EEEEEE;
    }

    .ellipsis {
        background-color: transparent;
        margin: 0;
    }

    @include media("above:small") {
        .ellipsis, .first-number, .last-number, .display {
            display: inline;
        }

        .number.extended {
            display: none;
        }

        .selected {
            border: 1px solid #6B6B6B;

            &:before {
                content: "";
            }
        }

        .previous {
            &:before {
                content: "";
            }
        }

        .after {
            &:after {
                content: "";
            }
        }
    }

    @include media("above:small") {
        .number {
            display: inline;
        }
    }

    @include media("above:medium") {
        .number.extended {
            display: inline;
        }
    }

    &.pagination--white-buttons {
        .previous, .number, .after {
            background-color: #ffffff;
        }

        a {
            color: #000000;
            background-color: #ffffff;

            &:link {
                color: #000000;
            }
            &:visited {
                color: #000000;
            }
            &:hover {
                color: #B91B1B;
                background-color: #ffffff;
            }
        }

        .disabled {
            color: #6B6B6B;
            background-color: transparent;
        }
    }
}

.pagination--news-intranet {
    margin: 0 0 5em;

    .after, .previous {

        &:hover {
            border: 1px solid #B91B1B;
            cursor: pointer;

            a {
                color: #B91B1B;
            }
        }
        a, &.disabled {
            border: none;
            cursor: initial;
        }
    }
}
