@mixin anchor-activable-stacked-featured() {
    display: block;
    border-style: $border-style-default;
    border-color: $color-emperor;
    border-width: 0 0 $border-width-hairline;

    padding-top: $measure-medium;
    padding-bottom: $measure-medium;

    &:hover,
    &[aria-selected="true"] {
        background: $color-gallery;
        color: $color-black;
    }
}

@mixin anchor-activable-inline-base() {
    @include transition;
    @include font-family(meta);
    border-bottom-width: 0;
    overflow: hidden;
    font-weight: $font-weight-bold;
    line-height: 1;
    text-decoration: none;

    &:hover {
        background-color: $transparent;
        color: $color-black;
    }

    &:focus {
        color: $color-black;
        outline: $border-width-hairline $border-style-alternate $color-black;
    }

    &:active {
        color: $color-black;
    }

    span {
        position: relative;
    }
}

@mixin anchor-activable-inline-normal($bgcolor: $color-yellow) {
    @include anchor-activable-inline-base;

    color: $color-black;
    background: #f2f2f2;
    padding: $measure-medium $measure-large;

    &:before {
        @include transition(opacity, transform);
        @include absolute(bottom-full-offset);
        content: "";
        opacity: 0;
        background-color: $bgcolor;
        transform: translateY(100%);
        bottom: 0;
        height: $border-width-thick;
    }

    &:hover,
    &[aria-selected="true"] {
        color: $color-black;
        background: $color-white;

        &:before {
            opacity: 1;
            transform: translateY(0);
        }
    }
}

@mixin anchor-activable-inline-air() {
  @include anchor-activable-inline-base;

  font-size: $font-size-alpha;
  font-weight: $font-weight-book;

  background-color: white;
  color: $color-black;
  padding: $measure-medium $measure-large;

  &:before {
    @include transition(opacity, width);
    position: absolute;
    bottom: 0;
    width: 0;
    content: "";
    opacity: 0;
    background-color: $color-alizarin;
    height: $border-width-thick;

    left: 30%;
    @include media("above:small") {
      left: 35%;
    }
  }

  &:hover,
  &[aria-selected="true"] {
    color: $color-black;
    background: $color-white;

    &:before {
      opacity: 1;

      width: 40%;
      @include media("above:small") {
        width: 30%;
      }
    }
  }
}


@mixin anchor-activable-stacked-normal($bgcolor: $color-yellow) {
    @include anchor-activable-inline-normal($bgcolor);
    display: block;
}

@mixin anchor-activable-inline-featured($bgcolor: $color-flamingo) {
    @include anchor-activable-inline-base;
    display: inline-block;

    color: $color-lucifer;

    @include media("below:medium") {
        padding: $measure-small $measure-medium $measure-medium;
    }

    @include media("above:medium") {
        padding: $measure-large $measure-medium;
    }

    &:before,
    span:before {
        @include transition(opacity, transform);
        content: "";
        opacity: 0;
        background-color: $bgcolor;
        transform: translateY(100%);
    }

    &:before {
        @include absolute(bottom-full-offset, $measure-medium);
        bottom: 0;
        height: #{$border-width-thick * 3};
    }

    span:before {
        @include absolute(top, 100%);
        width: 100%;
        height: $border-width-default;
    }

    &:hover {
        span:before {
            opacity: 1;
            transform: translateX(-50%) translateY(0);
        }
    }

    &[aria-selected="true"] {
        color: $color-black;

        &:before {
            opacity: 1;
            transform: translateY(0);
        }

        span:before {
            @include transition(none);
            opacity: 0;
        }
    }
}

@mixin anchor-activable-stacked-simple() {
    color: $color-black;
    background: $transparent;
    padding: $measure-medium $measure-large;

    &:before {
        background-color: $transparent;
    }
}

@mixin anchor-boxed-arrow() {
    border-bottom: none;
    padding: $measure-small;

    &:before {
        @include font-family(icon);
        content: "\f061";
        margin-right: $measure-medium;
        border: $border-width-hairline $border-style-default;
    }
}

