
.banner {
    position: relative;
}

.banner__container {
    @extend .container;
}

.banner__content {

    @include grid-column-map(("small": 12, "extralarge": 10));
    @include grid-column-push-map(("small": 0, "extralarge": 1));

    .banner__content__promo {
        left: 0;

        @include media("above:large") {
            width: 20%;
        }

        @include media("above:medium", "below:large") {
            width: grid-column-width(3);
        }

        @include media("above:between", "below:medium") {
            width: grid-column-width(4);
        }

        @include media("above:small", "below:between") {
            width: grid-column-width(6);
        }

        @include media("below:small") {
            width: auto;
            margin-left:  #{$measure-medium / 2};
            margin-right: #{$measure-medium / 2};
        }

        @include media("above:medium") {
            position: absolute;
            bottom: -#{$measure-medium * 2};
            z-index: z-index(navigation);
        }

        @include media("below:medium") {
            bottom: $measure-medium;
        }

        .banner--cutout--left & {
            right: 0;
            left: auto;

            @include media("above:small", "below:medium") {
                margin-left: auto;
            }
        }
    }

    .banner__content__text {
        padding-top: $measure-enormous;

        @include grid-column(6);
    }

    .banner__content__text__item {
        margin-bottom: $measure-small;
    }

    h1,
    .banner__content__text__subtitle {
        display: inline-block;
        margin: 0;
        padding: $measure-small $measure-medium;
        width: auto;
        font-size: $font-size-beta;
        @include font-family(meta);
    }

    .banner__content__text__subtitle,
    .banner__content__text__standfirst {
        font-weight: $font-weight-light;
    }

    .banner__content__text__standfirst {
        font-size: $font-size-gamma;
    }

    @include media("below:medium") {
        .banner__content__text__subtitle,
        .banner__content__text__standfirst {
            display: none;
        }
    }

    h1 {
        font-weight: $font-weight-book;
    }

    $themes: (
        corporate: (
            $color-alizarin,
            $color-white,
            $color-quill,
            $color-black
        ),
        undergraduate: (
            $color-flamingo,
            $color-black,
            $color-yellow,
            $color-black
        ),
        postgraduate: (
            $color-prussian,
            $color-white,
            $color-allports,
            $color-white
        ),
        phd: (
            $color-allports,
            $color-white,
            $color-picton,
            $color-black
        ),
        mba: (
            $color-sherpa,
            $color-white,
            $color-montecarlo,
            $color-black
        ),
        emba: (
            $color-stromboli,
            $color-white,
            $color-montecarlo,
            $color-black
        ),
        demba: (
            $color-stromboli,
            $color-white,
            $color-neptune,
            $color-black
        ),
        // demba: (
        //     $color-neptune,
        //     $color-white,
        //     $color-montecarlo,
        //     $color-black
        // )
        execed: (
            $color-vivid,
            $color-white,
            $color-violet,
            $color-black
        )
    );

    @each $theme, $colors in $themes {
        $title-background: nth($colors, 1);
        $title-color: nth($colors, 2);
        $subtitle-background: nth($colors, 3);
        $subtitle-color: nth($colors, 4);

        .banner--theme--#{$theme} & {
            h1 {
                background-color: $title-background;
                color: $title-color;
            }
            .banner__content__text__subtitle {
                background-color: $subtitle-background;
                color: $subtitle-color;
            }
        }
    }
}

// standfirst at bottom of banner:
.banner__standfirst {
    @extend .container;
}

.banner__standfirst__grid {
    @extend .grid;
}

.banner__standfirst__content {
    @include grid-column-map(("small": 12, "medium": 10, "extralarge": 8));
    @include grid-column-push-map(("small": 0, "medium": 1, "extralarge": 2));

    @include font-family(meta);
    font-size: $font-size-delta;
    color: $color-emperor;
    font-weight: $font-weight-light;
    text-align: center;
}
