@font-face {
	font-family: "iconfont";
	src: url('../fonts/iconfont/iconfont.eot');
	src: url('../fonts/iconfont/iconfont.eot?#iefix') format('eot'),
		url('../fonts/iconfont/iconfont.woff2') format('woff2'),
		url('../fonts/iconfont/iconfont.woff') format('woff'),
		url('../fonts/iconfont/iconfont.ttf') format('truetype'),
		url('../fonts/iconfont/iconfont.svg#iconfont') format('svg');
}



@mixin icon-styles {
    vertical-align: baseline;
    &:before {
        font-family: "iconfont";
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        font-style: normal;
        font-variant: normal;
        font-weight: normal;
        // speak: none; // only necessary if not using the private unicode range (firstGlyph option)
        text-decoration: none;
        text-transform: none;
        vertical-align: baseline;
    }
}

%icon {
	@include icon-styles;
}

@function icon-char($filename) {
	$char: "";

	@if $filename == angle-right {
		$char: "\E001";
	}
	@if $filename == arrow-left {
		$char: "\E002";
	}
	@if $filename == arrow-right {
		$char: "\E003";
	}
	@if $filename == back-to-top {
		$char: "\E004";
	}
	@if $filename == calendar-check {
		$char: "\E005";
	}
	@if $filename == close {
		$char: "\E006";
	}
	@if $filename == facebook {
		$char: "\E007";
	}
	@if $filename == instagram {
		$char: "\E008";
	}
	@if $filename == linkedin {
		$char: "\E009";
	}
	@if $filename == menu {
		$char: "\E00A";
	}
	@if $filename == minus {
		$char: "\E00B";
	}
	@if $filename == plus {
		$char: "\E00C";
	}
	@if $filename == search {
		$char: "\E00D";
	}
	@if $filename == share {
		$char: "\E00E";
	}
	@if $filename == twitter {
		$char: "\E00F";
	}
	@if $filename == weibo {
		$char: "\E010";
	}
	@if $filename == youtube {
		$char: "\E011";
	}

	@return $char;
}

@mixin icon($filename, $insert: before, $extend: true) {
    @if $extend {
        @extend %icon;
    } @else {
        @include icon-styles;
    }
	&:before {
		content: icon-char($filename);
	}
}

.icon-angle-right {
	@include icon(angle-right);
}
.icon-arrow-left {
	@include icon(arrow-left);
}
.icon-arrow-right {
	@include icon(arrow-right);
}
.icon-back-to-top {
	@include icon(back-to-top);
}
.icon-calendar-check {
	@include icon(calendar-check);
}
.icon-close {
	@include icon(close);
}
.icon-facebook {
	@include icon(facebook);
}
.icon-instagram {
	@include icon(instagram);
}
.icon-linkedin {
	@include icon(linkedin);
}
.icon-menu {
	@include icon(menu);
}
.icon-minus {
	@include icon(minus);
}
.icon-plus {
	@include icon(plus);
}
.icon-search {
	@include icon(search);
}
.icon-share {
	@include icon(share);
}
.icon-twitter {
	@include icon(twitter);
}
.icon-weibo {
	@include icon(weibo);
}
.icon-youtube {
	@include icon(youtube);
}


